<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>日常管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: 'return' }">返回审核记录</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="userName" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getReturnList">
            <el-button slot="append" icon="el-icon-search" @click="queryReturnByName(userName)"></el-button>
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="addVisible=true">添加申请</el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="danger" @click="batchDelete(tableChecked)">批量删除</el-button>
        </el-col>
      </el-row>

      <el-table
          :cell-style="rowClass"
          :data="returnList"
          :header-cell-style="headClass"
          border
          style="width: 100%"
          @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
            label="id"
            prop="id">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="userName">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="mobile">
        </el-table-column>
        <el-table-column
            label="身体状况"
            prop="stzk">
        </el-table-column>
        <el-table-column
            label="申请内容"
            prop="content">
        </el-table-column>
        <el-table-column
            label="审核状态"
            prop="status"
        >
        </el-table-column>
        <el-table-column
            width="180"
            label="申请时间时间"
            prop="createTime">
        </el-table-column>
        <el-table-column
            label="备注"
            prop="bz">
        </el-table-column>
        <el-table-column
            label="操作" prop="id">
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary"
                         @click="editReturn(scope.row)"></el-button>
            </el-tooltip>
            <el-popconfirm
                title="这是一段内容确定删除吗？"
                @confirm=removeUserItem(scope.row)
            >
              <el-button slot="reference" circle icon="el-icon-delete" size="mini" type="danger"></el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="pageNum"
          :page-size="pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>

    <!--添加dialog对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="添加记录"
        width="40%"
        @close="closeAddDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="ReturnUser" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="userName">
          <el-input v-model="ReturnUser.userName"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobile">
          <el-input v-model="ReturnUser.mobile"></el-input>
        </el-form-item>
        <el-form-item label="申请内容" prop="content">
          <el-input v-model="ReturnUser.content"></el-input>
        </el-form-item>
        <el-form-item label="近14天行程" prop="fourteenDay">
          <el-input v-model="ReturnUser.fourteenDay"></el-input>
        </el-form-item>
        <el-form-item label="身体状况" prop="stzk">
          <el-input v-model="ReturnUser.stzk"></el-input>
        </el-form-item>
        <el-form-item label="返回时间" prop="createTime">
          <el-date-picker
              style="float: left"
              v-model=ReturnUser.createTime
              placeholder="返回时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="ReturnUser.bz"></el-input>
        </el-form-item>
      </el-form>
    <el-button @click="addVisible = false">取 消</el-button>
    <el-button type="primary" @click="ConfirmAdd">确 定</el-button>
    </el-dialog>

    <!--修改dialog对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="添加记录"
        width="40%"
        @close="closeEditDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="ReturnUser" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="userName">
          <el-input v-model="ReturnUser.userName"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="mobile">
          <el-input v-model="ReturnUser.mobile"></el-input>
        </el-form-item>
        <el-form-item label="申请内容" prop="content">
          <el-input v-model="ReturnUser.content"></el-input>
        </el-form-item>
        <el-form-item label="近14天行程" prop="fourteenDay">
          <el-input v-model="ReturnUser.fourteenDay"></el-input>
        </el-form-item>
        <el-form-item label="身体状况" prop="stzk">
          <el-input v-model="ReturnUser.stzk"></el-input>
        </el-form-item>
        <el-form-item label="返回时间" prop="createTime">
          <el-date-picker
              style="float: left"
              v-model=ReturnUser.createTime
              placeholder="返回时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="ReturnUser.bz"></el-input>
        </el-form-item>
        <el-form-item label="审核结果" prop="status">
          <el-input v-model="ReturnUser.status"></el-input>
        </el-form-item>
      </el-form>
    <el-button @click="editVisible = false">取 消</el-button>
    <el-button type="primary" @click="ConfirmEdit">确 定</el-button>
    </el-dialog>


  </div>
</template>
<script>
export default {
  name: "ReturnApplication",
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      userName: '',
      pageNum: 1,
      pageSize: 5,
      total: 0,
      addVisible: false,
      editVisible: false,
      returnList: [],
      activeValue: "已审核",
      inactiveValue: "未审核",
      tableChecked: [],//被选中的记录数据-----对应“批量删除”传的参数值
      ids: [],//批量删除id
      ReturnUser: {},
      mobileFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
      },
    }
  },
  methods: {
    getReturnList() {
      this.$axios.get('/admin/return/queryReviewed',
          {
            params: {
              pageNum: this.pageNum, pageSize: this.pageSize
            }
          }).then(res => {
        if (res.data.status === 10000) {
          const list = [];
          const items = res.data.data.list;
          items.forEach(function (item, index) {
            if (item.status === "已审核") {
              list.push(item)
            }
          })
          this.total = res.data.data.total;
          this.returnList = list;
        } else {
          this.$message.error("请求数据失败")
        }
      })
    },
    handleSizeChange(newSize) {
      this.pageSize = newSize;
      this.getReturnList();
    },
    handleCurrentChange(newNum) {
      this.pageNum = newNum;
      this.getReturnList();
    },
    handleSelectionChange(val) {
      this.tableChecked = val;
    },
    ConfirmAdd() {
      console.log(this.ReturnUser, '增加返回申请')
      this.$axios.post('/return/add', this.ReturnUser).then(res => {
        console.log(res);
        this.addVisible = false;
      })
    },
    closeAddDialog() {
      this.$refs.addFormRef.resetFields();
    },
    batchDelete(rows) {
      rows.forEach((item, index) => {
        this.ids.push(item.id);
      });
      console.log(this.ids)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$axios.post('/admin/return/batchDelete', this.ids).then(res => {
          if (res.data.status !== 10000) {
            this.$message.error("删除失败！");
          }
          this.$message({type: 'success', message: '删除成功!'});
          this.getReturnList();
        });
        console.log('删除了', this.ids);
        this.ids = [];
      }).catch((e) => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    queryReturnByName(userName) {
      console.log(userName, "根据用户名查询")
      this.$axios.get("/return/queryByName", {params: {userName}}).then(res => {
        if (res.data.status === 10000) {
          this.returnList = [];
          this.returnList.push(res.data.data)
        } else {
          this.$message.error("请求数据失败")
        }
      })
    },
    editReturn(raw) {
      this.editVisible = true;
      this.ReturnUser = raw;
    },
    ConfirmEdit() {
      this.$refs.addFormRef.validate(valid => {
        if (valid) {
          this.$axios.post('/return/update', this.ReturnUser).then(res => {
            if (res.data.status === 10000) {
              this.$message.success('更新成功！')
              this.editVisible = false;
              this.getReturnList();
            }
          })
        } else {
          alert("请输入正确的信息")
        }
      })
    },
    closeEditDialog() {
      this.$refs.addFormRef.resetFields();
    },
    removeUserItem(row) {
      console.log('删除申请', row.id)
      this.$axios.get('/admin/return/delete', {params: {id: row.id}}).then(res => {

        if (res.data.status === 10000) {
          console.log("成功删除", row.username, "的信息")
          this.getReturnList()
        }
      })
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    }
  },
  created() {
    this.getReturnList();
  }
}

</script>

<style scoped>

</style>
